<template lang="html">
  <div class="Add-company">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
        <Col span="4" class="font-16">
          <h3>添加企业</h3>
        </Col>
        <Col span="5">

        </Col>
        <Col span="5">

        </Col>
        <Col span="8">
          <Button type="info" class="mgl-10" @click="succ">保存</Button>
          <Button type="warning" class="mgl-20" @click="cancel">取消</Button>
        </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>统一社会信用代码（营业执照注册号）<b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>组织机构代码</span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>税务登记号</span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>企业名称 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>单位类型 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>公司简称</span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>营业执照所在地 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>证件类型 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>联系电话 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>注册资本 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>营业期限 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>成立时间 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>营业范围 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>注册地址 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>代理人是否为企业法定代表人 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>传真 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>企业法定代表人联系电话  <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>企业法定代表人姓名  <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>实缴资本 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>企业法定代表人身份证号 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>营业执照登记机关 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>经营区域 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>营业执照核准日期 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>营业执照登记状态 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>是否存在境外交易 <b>*</b></span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      <Col span="10" class="text-right">
        <span>股东信息</span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="10" class="text-right">
        <span>收入及负债情况</span>
        <Input  placeholder="" style="width: 200px"></Input>
      </Col>
      
    </Row>
    <!-- <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">标题：</Col>
      <Col span="6" class="mgl-20"> <Input v-model="ContractName" placeholder="" style="width: 300px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="4" class="text-right">内容：</Col>
      <Col span="14" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{ editorContent }}</p></div>
      </Col>
    </Row> -->
    <!-- <Row type="flex" justify="start" class="code-row-bg contract-row">
      <Col span="4" class="text-right">发布时间：</Col>
      <Col span="6" class="mgl-20 text-left"> <DatePicker type="date" placeholder="" style="width: 200px"></DatePicker></Col>
    </Row> -->
  </div>
</template>

<script>
export default {
  data (){
    return {
      ContractName:'',
      ContractUse:'',
      editorContent: '',
    }
  },
  methods:{
    succ () {
      this.$Message.success('保存成功');
      setTimeout(() =>{
        this.$router.push('/Cooperative_enterprise');
      },2000)
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.ContractName = routerParams.name
          this.ContractUse = routerParams.use
          this.editorContent = routerParams.content
        }
    },
    cancel () {
      this.$router.push('/Cooperative_enterprise');
    }
  },
  mounted() {
    this.getParams ()
  }
}
</script>

<style lang="css">
.Add-company{
  background-color: #fff;
  padding: 20px;
}
.Add-company .search_criteria{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: center;
}
.Add-company .contract-row{
  line-height: 30px;
  margin-top: 20px;
}
.Add-company .ivu-select-dropdown{
  z-index: 99999
}
.Add-company .contract-row b{
  color: #ed3f14;
}
</style>



